import classNames from "classnames";
import * as style from "../assets/scss/index.module.scss";
import React from "react";
import {graphql, Link, useStaticQuery} from "gatsby";

const Tags: React.FunctionComponent = () => {
    const data = useStaticQuery(graphql`
        query {
            allMdx (filter: {internal: {type: {eq: "Mdx"}}, frontmatter: {published: {eq: true}}}) {
                distinct(field: frontmatter___tags)
            }
        }
    `)

    return <li className={classNames(style.navItem, style.gMy5)}>
            <h3 className={classNames(style.h5, style.gColorBlack, style.gFontWeight600, style.mb4)}>系列文章</h3>
            <ul className={classNames(style.uListInline, style.mb0)}>
                {
                    data.allMdx.distinct.map((item: string) => {
                        return <li className={classNames(style.listInlineItem, style.gMb10)} key={item}>
                            <Link className={classNames(style.uTagsV1,
                                style.gColorGrayDarkV4,
                                style.gColorWhiteHover,
                                style.gBgGrayLightV5,
                                style.gBgPurpleHover,
                                style.gFontSize12,
                                style.gRounded50,
                                style.gPy4,
                                style.gPx15)}
                               to={`/tags/${item}`}>
                                {item}
                            </Link>
                        </li>
                    })
                }
            </ul>
        </li>
}

export default Tags;